@import url("https://fonts.googleapis.com/css?family=Roboto");

html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  color: white;
  background-color: #4c91ad;
  font-family: "Roboto", Arial, Helvetica, sans-serif;
}

form {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.signupBox {
  width: 30%;
  position: relative;
  left: 50%;
  transform: translate(-50%, 20%);
  padding: 20px;
  background-color: #00000068;
  box-shadow: 0 0 20px 2px rgba(0, 0, 0, 0.4);
}

.signUpSubmit {
  margin: 25px 0 15px 0;
}

.pwStrRow {
  width: 63%;
  text-align: left;
  margin: auto;
  padding: 0;
  font-size: 12px;
}

.pwStrWeak {
  display: inline-block;
  position: relative;
  left: 0;
  padding: 0;
  margin: 0;
  color: gray;
}

.pwStrStrong {
  display: inline-block;
  position: relative;
  left: 77%;
  padding: 0;
  margin: 0;
  color: gray;
}

@media screen and (max-width: 550px) {
  .signupBox {
    width: 85% !important;
  }
}

.signupBox input::placeholder {
  font-size: 18px;
  font-family: "Numans", sans-serif;
}
.signupBox input:focus-visible {
  outline: none !important;
}
.signup-text {
  text-align: center;
  font-family: "Numans", sans-serif;
  font-size: 16px;
}
.signup-text a {
  color: red;
}
